.page-container {
  flex: 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 0.25rem; */
  border: 1px solid blue;
}
.page-btn {
  width: 100%;
  /* height: 100%; */
  display: flex;
  justify-content: space-between;
  /* border: 1px solid red; */
}

.prev-btn,
.next-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.35rem;
  /* margin: 0 0.2rem; */
  background-color: transparent;
  border-color: transparent;
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  border: 1px solid green;
}
.prev-btn:hover,
.next-btn:hover {
  color: #fff;
}
.prev-btn {
  margin-right: 0.2rem;
}
.next-btn {
  margin-left: 0.2rem;
}
.newPage-btn {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding: 0.35rem;
  /* margin: 0 0.2rem; */
  border: 1px solid green;
}
.newPage {
  padding: 0.25rem;
  background-color: transparent;
  border-color: transparent;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  /* border: 1px solid red; */
}
.activePage {
  color: #fff;
}
@media screen and (max-width: 479px) {
  .page-container {
    flex: 0 0 50px;
  }
  .prev-btn,
  .next-btn {
    /* padding: 0.35rem 0.5rem; */
    font-size: 0.85rem;
  }
  .prev-btn {
    margin-right: 0.3rem;
  }
  .next-btn {
    margin-left: 0.3rem;
  }

  .newPage {
    font-size: 0.85rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .page-container {
    flex: 0 0 55px;
  }
  .prev-btn,
  .next-btn {
    padding: 0.35rem 0.4rem;
    font-size: 0.9rem;
  }

  .prev-btn {
    margin-right: 0.4rem;
  }
  .next-btn {
    margin-left: 0.4rem;
  }

  .newPage {
    font-size: 0.9rem;
  }
}

@media screen and (min-width: 768px) {
  .page-container {
    flex: 0 0 60px;
    /* flex: 0 0 70px; */
  }

  .prev-btn,
  .next-btn {
    padding: 0.35rem 1.5rem;
    font-size: 1rem;
  }

  .prev-btn {
    /* margin-right: 1rem; */
    margin: 0 1rem;
  }
  .next-btn {
    /* margin-left: 1rem; */
    margin: 0 1rem;
  }
  .newPage {
    font-size: 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .page-container {
    flex: 0 0 65px;
  }
  .prev-btn,
  .next-btn {
    padding: 0.35rem 1.25rem;
    font-size: 1.1rem;
  }

  .prev-btn {
    /* margin-right: 0.5rem; */
    margin: 0 1.25rem;
  }
  .next-btn {
    /* margin-left: 0.5rem; */
    margin: 0 1.25rem;
  }
  .newPage {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1280px) {
  .page-container {
    flex: 0 0 70px;
  }
  .prev-btn,
  .next-btn {
    padding: 0.35rem 1.5rem;
    font-size: 1.2rem;
  }

  .prev-btn {
    /* margin-right: 0.5rem; */
    margin: 0 1.5rem;
  }
  .next-btn {
    /* margin-left: 0.5rem; */
    margin: 0 1.5rem;
  }
  .newPage {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1440px) {
  .page-container {
    flex: 0 0 75px;
  }
  .prev-btn,
  .next-btn {
    padding: 0.35rem 1.75rem;
    font-size: 1.3rem;
  }

  .prev-btn {
    margin-right: 0.5rem;
    margin: 0 1.75rem;
  }
  .next-btn {
    margin-left: 0.5rem;
    margin: 0 1.75rem;
  }
  .newPage {
    font-size: 1.3rem;
  }
}
