.single-movie-section {
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  overflow: hidden;
  /* overflow-y: auto; */
  border: 1px solid red;
}
.single-title {
  flex: 0 0 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.25rem;
  text-align: center;
  color: #fff;
  /* border: 1px solid red; */
}
.single-movie {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  gap: 0.25rem;
  padding: 0.25rem;
  overflow: hidden;
  /* border: 1px solid red; */
}

.single-center {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid blue; */
}
.single-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.movie-info {
  display: flex;
  flex-direction: column;
  padding: 0.25rem;
  overflow-y: auto;
  color: #fff;
  /* border: 1px solid blue; */
}
.movie-info p {
  display: flex;
  /* flex-direction: column; */
  flex-wrap: wrap;
  margin-bottom: 0.25rem;
  padding: 0.25rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  text-transform: capitalize;

  /* border: 1px solid red; */
}

.movie-info span {
  font-weight: 100;
  letter-spacing: normal;
  /* text-indent: 0.2rem; */
  margin: 0 0.25rem;
}
@media screen and (max-width: 479px) {
  .single-movie {
    grid-template-columns: repeat(2, 1fr);
  }
  .single-title {
    flex: 0 0 90px;
    font-size: 1.4rem;
  }
  .single-img {
    max-width: 400px;
  }

  .movie-info p {
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 1.1rem;
  }

  .movie-info span {
    font-size: 1rem;
  }
}
